import { combineLatest, BehaviorSubject } from "rxjs"
import { map, tap, switchMap, takeLast } from "rxjs/operators"
import { fromFetch } from 'rxjs/fetch'
import yaml from "js-yaml"

import system from "./system.js"


const sys = new BehaviorSubject(system({}, {}, {}, {}))
const prototypes = new BehaviorSubject([])

function ingestYaml(path) {
  return fromFetch(path).pipe(
    switchMap(response => response.text()),
    map(text => yaml.load(text))
  )
}


combineLatest([
  ingestYaml("./data/archetypes.yaml"),
  ingestYaml("./data/aspects.yaml"),
  ingestYaml("./data/classes.yaml"),
  ingestYaml("./data/powers.yaml")
]).pipe(
  map(([archetypes,
        aspects,
        classes,
        powers]) => system(archetypes, aspects, classes, powers))
).subscribe(sys)


combineLatest([
  ingestYaml("./data/prototypes.yaml"),
  sys
]).pipe(
  map(([prots, sys]) => prots.map(prot => sys.apply(prot))),
).subscribe(prototypes)


export {
  sys as system,
  prototypes
}
