<script>
import Action from "./Action.svelte"
export let doc
export let open = true
</script>

<section>
  <h4 on:click={() => open = !open} class="openable">{doc.name}</h4>
  <p><i>{doc.type}</i></p>
  {#if open}
    {#each doc.actions ? doc.actions : [] as action}
      <Action bind:doc={action}/>
    {/each}
  {/if}
</section>
