<script>
import Aspect from "./Aspect.svelte"
import Power from "./Power.svelte"

export let doc
export let open = true

function toggleOpen() {
  open = !open
}
</script>


<article>
  <h2 on:click={toggleOpen} class="openable">{doc.name}</h2>
  {#if open}
  <h3>Aspects</h3>
    {#each doc.aspects as aspect}
        <Aspect bind:doc={aspect}/>
    {/each}
  <h3>Powers</h3>
    {#each doc.powers as power}
        <Power bind:doc={power}/>
    {/each}
  {/if}
</article>

