<script>
import Passive from "./Passive.svelte"
export let doc
export let open = true
</script>

<section>
  <h4 on:click={() => open = !open} class="openable">{doc.name}</h4>
  <p><i>{doc.flavor}</i></p>
  {#if open}
    {#each doc.passives ? doc.passives : [] as passive}
      <Passive bind:doc={passive}/>
    {/each}
  {/if}
</section>
